:root {
  --primary-blue: #2764ff;
  --primary-black: #1c2127;
  --primary-pink: #fb0b67;
  --primary-yellow: #ffb918;
  --primary-chill: #85d1f0;
  --primary-angel: #ffffff;
  --primary-grey: #f5f5fe;
  --primary-gray: #cfd9e4;
  --sec-pink-1: #530d35;
  --sec-pink-2: #ea89b1;
  --sec-pink-3: #f9e5ec;
  --sec-blue-1: #213b93;
  --sec-blue-2: #adc0fa;
  --sec-blue-3: #eaf0fe;
  --sec-black-1: #000000;
  --sec-black-2: #737a91;
  --sec-black-3: #a7b1bc;
  --sec-yellow-1: #c49344;
  --sec-yellow-2: #f8d48b;
  --sec-yellow-3: #fceed4;
  --sec-chill-1: #5c8da4;
  --sec-chill-2: #b9dfef;
  --sec-chill-3: #daedf6;
  --support-success: #25c26e;
  --support-alert: #ff554a;
  --support-warning: #f0ad4e;
  --support-info: #0066f5;
  --light-1: #ffffff;
  --light-2: #f8f8f9;
  --light-3: #f1f1f1;
  --dark-1: #24273a;
  --dark-2: #1c2127;
  --dark-3: #20252b;
  --dark-4: #262932;
  --dark-5: #262b31;
  --dark-6: #373b3f;
  --dark-7: #393d41;
  --gradient-1: linear-gradient(40deg, #483beb, #7847e1, #dd568d);
  --gradient-2: linear-gradient(40deg, #c0cffe, #f3dff4, #f9d8e5);
  --gradient-3: linear-gradient(40deg, #abe2f3, #bde4e5, #ebe9d0);
  --gradient-4: linear-gradient(40deg, #6179fa, #98aafe, #fec536);
}

.convert {
  padding: 100px 15%;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: auto;
  background-image: var(--gradient-2);

  h1 {
    font-weight: 800;
    font-size: max(30px, 20px);
    line-height: 1.2;
    width: 80%;
  }
  .convert-details {
    width: 50%;

    p {
      font-weight: 600;
      width: 400px;
      margin-top: 10px;
      margin-bottom: 20px;
    }
    ul {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 20px;
    }
    span {
      width: 20px;
      height: 10px;
      border-radius: 50px;
      background: var(--sec-chill-2);
      padding: 3px 7px;
    }
  }
  .convert-function {
    display: flex;
    flex-direction: column;
    flex-basis: 40%;
    background: var(--light-1);
    border-radius: 10px;
    padding: 1rem;
    width: 50%;
    gap: 15px;

    h1 {
      font-size: 20px !important;
      width: 100%;
    }
    p {
      font-weight: 700;
      width: 100%;
    }

    form {
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: 15px;
    }
    .convert-wrapper-block {
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding: 3px 5px;
      border-radius: 9px;
      background-color: var(--light-1);
      border: none;
      outline: none;
      box-shadow: 0px 0px 1px 2px var(--primary-gray);

      select {
        background: var(--primary-gray);
        cursor: pointer;
        padding: 4px 15px;
        border: none;
        outline: none;
        position: relative;

        border-radius: 6px;
      }
      input {
        flex: 1;
        outline: none;
        border: none;
      }
    }
    span {
      background-color: var(--light-3);
      color: var(--primary-blue);
      border-radius: 100%;
      width: 30px;
      font-weight: 800;
      text-align: center;
      cursor: pointer;
      display: inline;
      height: 30px;
      box-shadow: 0px 0px 6px 2px var(--light-3);
    }
  }
}
