@tailwind base;
@tailwind components;
@tailwind utilities;

:root{
    --primary-blue: #2764FF;
    --primary-black: #1C2127;
    --primary-pink: #FB0B67;
    --primary-yellow: #FFB918;
    --primary-chill: #85D1F0;
    --primary-angel: #ffffff;
    --primary-grey: #F5F5FE;
    --primary-gray: #CFD9E4;
    --sec-pink-1: #530D35;
    --sec-pink-2: #EA89B1;
    --sec-pink-3: #F9E5EC;
    --sec-blue-1: #213B93;
    --sec-blue-2: #ADC0FA;
    --sec-blue-3: #EAF0FE;
    --sec-black-1: #000000;
    --sec-black-2: #737A91;
    --sec-black-3: #A7B1BC;
    --sec-yellow-1: #C49344;
    --sec-yellow-2: #F8D48B;
    --sec-yellow-3: #FCEED4;
    --sec-chill-1: #5C8DA4;
    --sec-chill-2: #B9DFEF;
    --sec-chill-3: #DAEDF6;
    --support-success: #25C26E;
    --support-alert: #FF554A;
    --support-warning: #F0AD4E;
    --support-info: #0066F5;
    --light-1: #FFFFFF;
    --light-2: #F8F8F9;
    --light-3: #F1F1F1;
    --dark-1: #24273A;
    --dark-2: #1C2127;
    --dark-3: #20252B;
    --dark-4: #262932;
    --dark-5: #262B31;
    --dark-6: #373B3F;
    --dark-7: #393D41;
    --gradient-1: linear-gradient(40deg, #483BEB, #7847E1, #DD568D);
    --gradient-2: linear-gradient(40deg, #C0CFFE, #F3DFF4, #F9D8E5);
    --gradient-3: linear-gradient(40deg, #ABE2F3, #BDE4E5, #EBE9D0);
    --gradient-4: linear-gradient(40deg, #6179FA, #98AAFE, #FEC536);
}
.app{
    padding: auto 15%;
    margin: auto;
    width: 100%;
    background-color: #fdeeeb;
}