@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary-blue: #2764ff;
  --primary-black: #1c2127;
  --primary-pink: #fb0b67;
  --primary-yellow: #ffb918;
  --primary-chill: #85d1f0;
  --primary-angel: #ffffff;
  --primary-grey: #f5f5fe;
  --primary-gray: #cfd9e4;
  --sec-pink-1: #530d35;
  --sec-pink-2: #ea89b1;
  --sec-pink-3: #f9e5ec;
  --sec-blue-1: #213b93;
  --sec-blue-2: #adc0fa;
  --sec-blue-3: #eaf0fe;
  --sec-black-1: #000000;
  --sec-black-2: #737a91;
  --sec-black-3: #a7b1bc;
  --sec-yellow-1: #c49344;
  --sec-yellow-2: #f8d48b;
  --sec-yellow-3: #fceed4;
  --sec-chill-1: #5c8da4;
  --sec-chill-2: #b9dfef;
  --sec-chill-3: #daedf6;
  --support-success: #25c26e;
  --support-alert: #ff554a;
  --support-warning: #f0ad4e;
  --support-info: #0066f5;
  --light-1: #ffffff;
  --light-2: #f8f8f9;
  --light-3: #f1f1f1;
  --dark-1: #24273a;
  --dark-2: #1c2127;
  --dark-3: #20252b;
  --dark-4: #262932;
  --dark-5: #262b31;
  --dark-6: #373b3f;
  --dark-7: #393d41;
  --gradient-1: linear-gradient(40deg, #483beb, #7847e1, #dd568d);
  --gradient-2: linear-gradient(40deg, #c0cffe, #f3dff4, #f9d8e5);
  --gradient-3: linear-gradient(40deg, #abe2f3, #bde4e5, #ebe9d0);
  --gradient-4: linear-gradient(40deg, #6179fa, #98aafe, #fec536);
}
.wallet,
.exchange,
.developers,
.business {
  margin: 100px 13%;
  border-radius: 13px;
  padding: 1.5rem;
  display: flex;
  width: auto;
  background-image: url(../../images/offerbg.png);
  h2 {
    color: var(--support-info);
    font-weight: 600;
    letter-spacing: 5px;
  }
  h1 {
    font-size: max(2.5vw, 30px);
    font-weight: bolder;
  }
  p {
    font-size: 16px;
  }
  ul {
    display: inline-flex;
    margin-top: 30px;
    justify-content: space-between;
    flex-wrap: wrap;
    display: grid;
    grid-template-columns: auto auto;
    gap: 25px;
  }
  li {
    margin-left: 4px;
  }
  span {
    width: 20px;
    height: 10px;
    border-radius: 50px;
    background: var(--sec-chill-2);
    padding: 3px 7px;
  }

  .wallet-title {
    max-width: 550px;
  }
}
.wallet-btns {
  display: flex;
  gap: 10px;
  align-items: center;
  margin-top: 30px;
}
.wallet-btns button:last-child {
  color: var(--support-info);
  font-weight: 700;
}

@media (max-width:1024px){
    .wallet, .exchange, .developers, .business{
        margin: 100px 7%;
        ul{
            gap: 10px;
        }
    }
}

@media  (max-width:768px) {
    .wallet,.business, .exchange,.developers{
        flex-direction: column;
        margin: 100px 3%;
        .image-wrapper{
            display: none;
        }
        ul{
            grid-template-columns: 1fr;
        }
        .wallet-btns{
            /* display: block; */
            flex-direction: column;
            justify-content: left;
            align-items: start;

        }
    }
    
}