@tailwind base;
@tailwind components;
@tailwind utilities;
*{
    padding: 0;
    margin: 0;
}
:root {
  --primary-blue: #2764ff;
  --primary-black: #1c2127;
  --primary-pink: #fb0b67;
  --primary-yellow: #ffb918;
  --primary-chill: #85d1f0;
  --primary-angel: #ffffff;
  --primary-grey: #f5f5fe;
  --primary-gray: #cfd9e4;
  --sec-pink-1: #530d35;
  --sec-pink-2: #ea89b1;
  --sec-pink-3: #f9e5ec;
  --sec-blue-1: #213b93;
  --sec-blue-2: #adc0fa;
  --sec-blue-3: #eaf0fe;
  --sec-black-1: #000000;
  --sec-black-2: #737a91;
  --sec-black-3: #a7b1bc;
  --sec-yellow-1: #c49344;
  --sec-yellow-2: #f8d48b;
  --sec-yellow-3: #fceed4;
  --sec-chill-1: #5c8da4;
  --sec-chill-2: #b9dfef;
  --sec-chill-3: #daedf6;
  --support-success: #25c26e;
  --support-alert: #ff554a;
  --support-warning: #f0ad4e;
  --support-info: #0066f5;
  --light-1: #ffffff;
  --light-2: #f8f8f9;
  --light-3: #f1f1f1;
  --dark-1: #24273a;
  --dark-2: #1c2127;
  --dark-3: #20252b;
  --dark-4: #262932;
  --dark-5: #262b31;
  --dark-6: #373b3f;
  --dark-7: #393d41;
  --gradient-1: linear-gradient(40deg, #483beb, #7847e1, #dd568d);
  --gradient-2: linear-gradient(40deg, #c0cffe, #f3dff4, #f9d8e5);
  --gradient-3: linear-gradient(40deg, #abe2f3, #bde4e5, #ebe9d0);
  --gradient-4: linear-gradient(40deg, #6179fa, #98aafe, #fec536);
}
.navbar{
    display: flex;
    background: var(--light-2);
    align-items: center;
    width: 100%;
    position: fixed;
    overflow: hidden;
    padding: 25px 15%;

    a{
        cursor: pointer;
    }

    ul{
        display: flex;
        margin-left: auto;
        gap: 20px;
    }
    .dropdowns-btns{
        display: flex;
        gap: 8px;
    }
    select{
        width: 100%;
        padding: 0;
        font-weight: 900;
        cursor: pointer;
    }
    .buttons{
        display: flex;
        gap: 9px;
    }
    .nav-right{
        margin-left: auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 25px;
    
    }
}

.signup {
    background: var(--gradient-1);
    padding: 6px 37px;
    border-radius: 10px;
    color: var(--light-3);
    font-weight: bolder;
}
.menu{
    display: none;
}
.navbar-wrapper{
    display: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    position: relative;
}
@media (max-width:1024px){
    .navbar{
    padding: 20px 12%;

    .buttons{
        gap:20px
    }

    }
}

@media (max-width:768px){
  .navbar{
    padding: 20px 3%;
    background: initial ;
    position: fixed;
    display: block;

  
    ul{
        display: block;
        margin: 20px;
    }
      .menu{
        display: block;
        position: absolute;
        background: var(--primary-blue);
        padding: 0px 18px;
        cursor: pointer;
        text-align: center;
        font-size: 20px;
        font-weight: 900;
        border-radius: 4px;
        right: 10px;
    }
   .nav-right, .buttons{
    display: flex ;
    flex-direction: column;
    margin: 40px 0;
   }
   .dropdowns-btns{
    margin: 10px 0px !important;
   }
  }
  .navbar-wrapper{
        display: none;
        position: fixed;
        /* display: flex; */
        /* flex-direction: column; */
       
    }
    .navbar-wrapper.lopen{
        display: block;
        background-color: var(--light-1);
         float: right;
        top: 60px;
        width: 200px;
        height: 100%;
        right: 0;

    }
}
