:root {
  --primary-blue: #2764ff;
  --primary-black: #1c2127;
  --primary-pink: #fb0b67;
  --primary-yellow: #ffb918;
  --primary-chill: #85d1f0;
  --primary-angel: #ffffff;
  --primary-grey: #f5f5fe;
  --primary-gray: #cfd9e4;
  --sec-pink-1: #530d35;
  --sec-pink-2: #ea89b1;
  --sec-pink-3: #f9e5ec;
  --sec-blue-1: #213b93;
  --sec-blue-2: #adc0fa;
  --sec-blue-3: #eaf0fe;
  --sec-black-1: #000000;
  --sec-black-2: #737a91;
  --sec-black-3: #a7b1bc;
  --sec-yellow-1: #c49344;
  --sec-yellow-2: #f8d48b;
  --sec-yellow-3: #fceed4;
  --sec-chill-1: #5c8da4;
  --sec-chill-2: #b9dfef;
  --sec-chill-3: #daedf6;
  --support-success: #25c26e;
  --support-alert: #ff554a;
  --support-warning: #f0ad4e;
  --support-info: #0066f5;
  --light-1: #ffffff;
  --light-2: #f8f8f9;
  --light-3: #f1f1f1;
  --dark-1: #24273a;
  --dark-2: #1c2127;
  --dark-3: #20252b;
  --dark-4: #262932;
  --dark-5: #262b31;
  --dark-6: #373b3f;
  --dark-7: #393d41;
  --gradient-1: linear-gradient(40deg, #483beb, #7847e1, #dd568d);
  --gradient-2: linear-gradient(40deg, #c0cffe, #f3dff4, #f9d8e5);
  --gradient-3: linear-gradient(40deg, #abe2f3, #bde4e5, #ebe9d0);
  --gradient-4: linear-gradient(40deg, #6179fa, #98aafe, #fec536);
}
.hero {
  padding: 150px 15%;
  text-align: center;
  background: var(--gradient-2);
  /* background:linear-gradient(90deg,#394be7 32.81%,#ea577e)        ; */

  h2 {
    background: var(--primary-gray);
    border-radius: 20px;
    margin: auto;
    color: var(--primary-blue);
    font-weight: 800;
    width: 450px;

    span {
      background: var(--primary-blue);
      color: var(--light-1);
      border-radius: 15px;
      padding: 2px 10px;
      margin: auto;
    }
  }
  h1 {
    font-weight: 900;
    font-size: 3.5em;
    margin-bottom: 20px;
    line-height: 1;
  }
  p {
    font-size: 18px;
    margin-bottom: 40px;
  }
  input {
    outline: none;
    border: 0;
    font-weight: 600;
    padding-left: 4px;
  }
  b {
    background: var(--light-1);
    border-radius: 6px;
    padding: 1px 10px;
  }
  img {
    margin: auto;
  }
}
.email-wrapper {
  margin: 40px auto;
  display: flex;
  padding: 10px 5px;
  justify-content: space-between;
  border-radius: 10px;
  width: 400px;
  background: var(--light-1);
}
.reviews {
  background-color: var(--light-1);
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  padding: 10px 2rem;
  text-align: left;
  height: 100px;
  margin-top: 40px;

  strong {
    font-size: 25px;
  }
}
.offer-news {
  margin-top: 80px;
  h3 {
    background: var(--primary-grey);
    color: var(--primary-blue);
    width: 140px;
    border-radius: 20px;
    font-weight: 700;

    margin: 30px auto;
  }
  h2 {
    color: #000000;
    font-size: 25px;
    line-height: 1.2;
    margin-bottom: 10px;
    background: none;
  }
}
.download-text{
    display: block;
}
@media (max-width:768px){
    .hero{
        text-align: left;
        padding: 100px 3% !important;
        .download-text{
            display: none;
        }
        h1{
            font-size: 30px;
            margin-top: 20px;
        }
        h2{
            width: auto;
        }
        input{
            display: none;
        }
        .email-wrapper{
            background: transparent;
        }
        .reviews{
            flex-direction: column;
            height: 100%;
        }
        .offer-news{
            text-align: left;
            h3{
                text-align: left;
                margin: 20px 0;
                padding: 2px 3px;
            }
        }
    
    }
    
}
